.landingPageContainer {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

#mainImage {
    width: 50%;
    height: 95%;
    max-height: 750px;
    overflow: hidden;
    border: 10px solid #2fd5cd;
}

#mainImage img {
    width: 100%;
}

/* Right side with texts*/

.textContainer {
    width: 40;
    margin: 0 auto;
    padding: 2rem;
}

.textContainer p {
    line-height: 1.75;
}

.textContainer h2 {
    font-size: 2.5rem;
    margin-top: 0;
}

.textContainer h2 span {
    color: #2fd5cd;
    font-weight: 100;
}

.textContainer h3 span {
    color: #2fd5cd;
    text-transform: uppercase;
}

.textContainer a {
    text-decoration: none;
    color: #2fd5cd;
    font-weight: bold;
    font-style: italic;
    padding-bottom: 3px;
}

.textContainer a:hover {
    border-bottom: 1px solid #2fd5cd;
}

.textContainer button {
    background-color: #2fd5cd;
    width: 100px;
    padding: 10px;
    margin-right: 10px;
}

.textContainer button:hover {
    background-color: #333;
}

.textContainer a {
    color: white;
    font-style: normal;
}

.textContainer a:hover {
    border: none;
}

/* Media Queries */

@media only screen and (max-width: 1000px) {
    .landingPageContainer {
        flex-wrap: wrap;
    }
    #mainImage {
        width: 100%;
        max-height: 400px;
        border: 10px solid #2fd5cd !important;
    }
    .textContainer {
        text-align: center;
    }
}